body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-color: #282c34; */
  min-height: 100vh;

  max-width: 1440px;
  margin: auto;
  display: grid;
  grid-template-columns: 0.1fr 4fr 0.1fr;
  grid-template-rows: 0.1fr 0.1fr 2.2fr 0.1fr;
  grid-template-areas: "title title title" ". board ." ". utils ." ". . .";
}

div {
  /* border: 1px solid rgb(72, 6, 6); */
}

.title { grid-area: title; }

.board { grid-area: board; }

.utils { grid-area: utils; }

.App button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 0.5vh 10vh;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 2vh;
}

.App button:hover {
  background-color: rgb(17, 230, 191); /* Green */
  color: white;
}

.App input {
  width: 90%;
  height: 90%;
  color: #282c34;
}
.title {
  height: 80%;
  width: 100%;
  font-size: 5vh;
  color: rgb(31, 230, 230);
  padding: 0vh;
  margin: 1vh;
}
.Board {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "b2 b4 b6 b8 b12 b14 b16 b18 sbkV";
  grid-area: board;
  height: 100%;
  margin: 0vh;
  padding: 0vh;
  color: azure;
  background-color: rgb(7, 87, 86);
  text-align: center;
  border-radius: 2vh;
}

Statistic {
  color: azure;
}

.Board div {
  margin: 0vh;
  padding: 0.5vh;
}

.blkN0Title {
  grid-area: b1;
}

.blkNoValue {
  grid-area: b2;
}

.totalstkTitle {
  grid-area: b3;
}

.totalstkValue {
  grid-area: b4;
}

.everageTitle {
  grid-area: b5;
}

.everagetValue {
  grid-area: b6;
}

.minerCntTitle {
  grid-area: b7;
}

.minerCntValue {
  grid-area: b8;
}

.delegatorCntTitle {
  grid-area: b9;
}

.delegatorCntValue {
  grid-area: b10;
}

.delegatePartCntTitle {
  grid-area: b11;
}

.delegatePartCntValue {
  grid-area: b12;
}

.epochIDTitle {
  grid-area: b13;
}

.epochID {
  grid-area: b14;
}

.slotIDTitle {
  grid-area: b15;
}

.slotID {
  grid-area: b16;
}

.epochPercentTitle {
  grid-area: b17;
}

.epochPercent {
  grid-area: b18;
}

.curEpochTime {
  grid-area: b19;
}

.curEpochTimeValue {
  grid-area: b20;
}

.nextEpochTime {
  grid-area: b21;
}

.nextEpochTimeValue {
  grid-area: b22;
}

.sbkT {
  grid-area: sbkT;
}

.sbkV {
  grid-area: sbkV;
}
.Utils {
  min-height: 50vh;
  margin: 1vh 0vh 0vh 0vh;
}
.Reward {
  height: 300 px;
  border-radius: 2vh;
  background-color: rgb(50, 66, 99);
  margin: 1vh 0vh 0vh 0vh;
}

.Reward div {
  padding: 1vh;
  color: white;
}

.rewardTitle {
  border-bottom: 1px solid rgb(174, 167, 167);
}

.rewardGrid {
  height: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "i o";
}

.i {
  display: grid;
  grid-template-columns: 0.5fr 1.5fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas: "i1 i11" "i2 i21" "i3 i31" "bt bt";
  grid-area: i;
}

.i1 { grid-area: i1; }

.i2 { grid-area: i2; }

.i3 { grid-area: i3; }

.bt { grid-area: bt; }

.i11 { grid-area: i11; }

.i21 { grid-area: i21; }

.i31 { grid-area: i31; }

.o { 
  grid-area: o; 
  background-color: rgb(47, 78, 79);
  border-radius: 2vh;
}

.ant-message {
  color: black;
}
.LeaderBoard {
  min-height: 300px;
  border-radius: 2vh;
  background-color: rgb(59, 96, 121);
  margin: 1vh 0vh 0vh 0vh;
}

.LeaderBoard div {
  padding: 0.4vh;
}

.LeaderBoardTitle {
  /* border-bottom: 1px solid rgb(174, 167, 167); */
  font-size: 16px;
  color: rgb(255, 255, 255);
}

.LeaderBoardBody {
  padding: 1vh;
}

Table {
  color: rgb(252, 252, 252);
}

.ant-table-column-title {
  color: rgb(255, 255, 255);
}
